export async function getServerSideProps({ query }) {
  const page = parseInt(query.page) || 1;
  const limit = 20;
  const offset = (page - 1) * limit;

  try {
    // Fetch the paginated products from your API
    const res = await fetch(`http://localhost:4000/products?limit=${limit}&offset=${offset}`);
    
    if (!res.ok) {
      throw new Error('Failed to fetch products');
    }
    
    const data = await res.json();
    
    if (!data || !data.products) {
      throw new Error('Invalid product data');
    }

    // Simulating the total count for pagination (you could get this from your database or API)
    const totalProducts = 3000; // Assume total product count
    const totalPages = Math.ceil(totalProducts / limit);

    return {
      props: {
        products: data.products || [],  // Ensure products is an array or empty array
        page,
        totalPages,
      },
    };

  } catch (error) {
    console.error('Error fetching products:', error);
    
    return {
      props: {
        products: [],  // Return an empty array if something goes wrong
        page,
        totalPages: 1, // Adjust totalPages based on your error handling
      },
    };
  }
}
